import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController } from 'ionic-angular';
import { ConcertsProvider } from '../../providers/concerts/concerts';
/**
 * Generated class for the UserAdminPage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-user-admin',
  templateUrl: 'user-admin.html',
})
export class UserAdminPage {
  users: any;
  constructor(public navCtrl: NavController, public navParams: NavParams, private concertService: ConcertsProvider, private alertCtrl: AlertController) {
  }

  ionViewDidLoad() {
    this.refreshData();
  }

  refreshData() {
    this.concertService.getAllUsers().then(users => {
      this.users = users;
    });
  }
  doRefresh(refresher) {
    this.refreshData();
    refresher.complete();
    console.log('Feeling Re-Fresh`d?');
  }
  newUser() {
    let prompt = this.alertCtrl.create({
      title: 'Opprette bruker',
      message: "Skriv inn navn for brukeren",
      inputs: [
        {
          name: 'name',
          placeholder: 'Navn'
        },
      ],
      buttons: [
        {
          text: 'Cancel',
          handler: data => {
            console.log('Cancel clicked');
          }
        },
        {
          text: 'Save',
          handler: data => {
            console.log('Saved clicked');
            this.concertService.addNewUser({ name: data.name, concerts: [], passes: null }).then(user => {
              this.goToUserAdminDetailPage(user);
            }
            );
          }
        }
      ]
    });
    prompt.present();
  }

  goToUserAdminDetailPage(user) {
    this.navCtrl.push('EditUserPage', { 'user': user });
  }

  presentConfirmDelete(user) {
    let alert = this.alertCtrl.create({
      title: 'Fjerne bruker',
      message: 'Er du sikker pÃ¥ at du vil fjerne denne brukeren?' + user.name,
      buttons: [
        {
          text: 'Avbryt',
          role: 'cancel',
          handler: () => {
            console.log("Cancelled");

          }
        },
        {
          text: 'Fjern Bruker',
          handler: () => {
            this.deleteUser(user);
            this.refreshData();
          }
        }
      ]
    });
    alert.present();
  }

  deleteUser(user) {
    // TODO: implement
    this.concertService.deletUser(user._id);
    this.refreshData();
  }

}



